import React, { useRef, Suspense } from "react";
// import components
import Navbar from "./components/Navbar";
import Home from "./components/Home";
import Services from "./components/Services";
import HireMe from "./components/HireMe";
import { Helmet } from "react-helmet";
// style sheet
import "./App.scss";
const About = React.lazy(() => import("./components/AboutMe"));
const Projects = React.lazy(() => import("./components/Projects"))



function App() {

  const mainDiv = useRef()

  return (
    <div className="App">
      <div className="App-header" ref={mainDiv}>
        <Helmet>
          <meta name="author" content="Mahmoud Essam" />
          <meta name="description"
            content="Mahmoud Essam portfolio, you will see my latest projects , my skills and my resume . here you will find my frontend , backend and fullstack projects . and you can contact me from the form in the bottom of the page." />
          <meta name="keywords" content="mahmoud,essam,mamoudessam,react,portfolio,essaminreact,previous work, frontend developer, backend developer, fullstack web developer, restful api, rest api, react.js, angular, node.js, mongodb, express.js, MEARN stack developer." />
        </Helmet>
        <Navbar mainDiv={mainDiv} scroll={window} />
        <Home />
        <Suspense fallback={<div>loading...</div>}>
          <About />
        </Suspense>
        <Services />
        <Suspense fallback={<div>loading...</div>}>

          <Projects />
        </Suspense>
        <HireMe />
      </div>
    </div>
  );
}

export default App;
