import React from 'react'
import { Col } from "react-bootstrap";
import './scss/Card.scss'
function Card({ icon, header, par }) {
    return (
        <Col lg={4} md={6}>
            <div className="card-wrapper">
                <div className="icon">
                    {icon}
                </div>
                <h3>{header}</h3>
                <p>
                    {par}
                </p>
            </div>
        </Col>
    )
}

export default Card
